.logoscorp {


    // Offices


    .offices-selection {

        .accordion {
            display: flex;
            border-top: 1px solid $light-gray;

        }

        .offices-select {
            margin-bottom: 30px;
        }

        h2 {
            text-align: center;
            margin-bottom: 15px;
        }

        h6 {

        }

        #flush-headingOne {
            width: 100%;
        }

        #flush-headingOne:before {
            content: url(/wp-content/uploads/2021/06/001_ICONO_GPS.svg);
        }

        .offices-by-state {
            flex-direction: column;
            padding: 20px 0 18px;
            
        }
        .all-offices {
            width: 100%;
        }

        .offices-group {
            width: 100%;
        }
 

        .offices-selection {

            .offices-list {
                grid-template-columns: 1fr;
            }
        }
    }
}