.logoscorp {


    /*********************************
        Header
    **********************************/

    .et-l--header {

        .tc-header {

            .box-header-title,
            .et_pb_module_header,
            p {
                display: none;
            }

            .et_pb_button_module_wrapper {
                text-align: center;
            }

            .et_pb_menu__wrap {
                &:after {
                    display: none;
                }
            }

            .et_pb_menu__search-button:before {
                content: '';
                width: 1px;
                height: 16px;
                background: $medium-gray;
                margin-left: 16px;

            }

            .et_pb_menu__search-button:after {
                margin: 0 4px 0 18px;
            }

            .tc-header-menu {
                .mobile_nav .mobile_menu_bar:before {
                    color: $dark-gray;
                }
            }
        }

    }

    .tc-home-tracking {

        padding: 10px;
        display: block;

        .home-calc {
            display: flex;
            align-items: center;

            img {
                margin-right: 25px;
            }
        }

        .et_pb_column:first-child {
            margin-bottom: 10px;
        }

        .et_pb_column.et-last-child {

            grid-template-columns: 1fr;
            gap: 10px 0;
        }

        .tracking-title {
            margin-bottom: 10px
        }
    }

    /*********************************
        Calculadora de Tarifas
    **********************************/

    .entry-content {

        .rates-form {
            #rates-result-table {
                tbody {
                    display: grid;
                    grid-template-columns: 1fr;
                }

                tr {
                    border-right: none;
                }

                tr th {
                    border-top: none;
                    border-right: none;
                    font-family: 'Graphik Semibold', Helvetica, Arial, Lucida, sans-serif;
                }

                tr:last-child th {
                    border-top: none;
                }
            }

            .select2.select2-container {
                max-width: none;
            }

            .pv-label {
                margin-bottom: .8em;
            }

            .form-mandatory-msg-1 {
                display: none;
            }

            .form-mandatory-msg-2 {
                display: block;
                margin-bottom: 30px;
            }

        }

    }

    /*********************************
        Blog
    **********************************/

    .tc-blog {

        .et_pb_ajax_pagination_container {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    /*********************************
        Footer
    **********************************/
    .tc-footer {
        ul.footer-links {

            li:first-child {
                padding: 1em 0 .25em;
            }

            li {
                padding: .25em 0 .25em;
            }
        }
    }

    /*********************************
        Rows
    **********************************/

    .tc-row {
        padding: 0;
    }


    /*********************************
        Services
    **********************************/
    .tc-home-services h3 {
        font-size: 2.8rem;
        //padding-right: 50%;
        line-height: 1.15em;
    }

}