.logoscorp {


    /*********************************
        Rows
    **********************************/

    .tc-row {
        .et_pb_column:first-child {
            .et_pb_text {
                h2 {
                    font-size: 2.5rem;
                }
            }
        }
    }


    .tc-header-2 h1 {
        font-size: 1.75rem;
    }

    /*********************************
        Blog
    **********************************/

    .tc-blog {

        .et_pb_ajax_pagination_container {
            grid-template-columns: 1fr;
        }
    }

    /*********************************
        Newsletter
    **********************************/

    .tc-newsletter {

        .et_pb_newsletter_field {
            flex-basis: 100%;

        }
        
        .et_pb_newsletter_button_wrap {
            flex-basis: 100%;
        }
    }
    

    .tc-home-services h3 {
        font-size: 2.5rem;
        padding-right: 10%;

    }
}