.logoscorp {
    .tc-office-map-app {
        display: flex;
        flex-direction: column-reverse;

        .et_pb_button {
            clip-path: polygon(88% 0, 100% 50%, 100% 100%, 0 100%, 0 0);
            margin-bottom: 20px;
        }
    }

    /*********************************
        Offices
    *********************************/
    
    .banner-office-datail {
        .office-detail-title {
            font-size: clamp(40px, 4.17vw, 80px);
        }
    }

    .offices-selection {

        .accordion {
            display: flex;
            border-top: 1px solid $light-gray;

        }

        .offices-select {
            margin-bottom: 30px;
        }

        h2 {
            font-size: 28px;
            font-weight: 700;
            margin-bottom: 0;
            margin-right: 4%;
            padding: 5px 0 0 0;
            min-width: 157px;
            width: 157px;
        }

        h6 {
            color: $dark-blue;
            font-size: 1rem;
            font-weight: 700;
            padding: 0.425em 2em 0.325em;
            background-color: #F6F6F6;
        }

        #flush-headingOne {
            display: flex;
            align-items: center;
            margin: 0 10px 10px 0
        }

        #flush-headingOne:before {
            content: url(/wp-content/uploads/2021/06/001_ICONO_GPS.svg);
            margin-right: 10px;
        }

        .offices-list {

            display: grid;
            grid-template-columns: repeat(4, 1fr);
        }

        .offices-by-state {
            display: flex;
            padding: 40px 0 35px;
            border-bottom: 1px solid $light-gray;
            width: 100%;

        }

        .all-offices {
            width: 100%;
        }

        .offices-group {
            width: 100%;
        }


    }
}