.logoscorp {


    .clip-button{
        clip-path: polygon(88% 0, 100% 50%, 100% 100%, 0 100%, 0 0);

    }

    /*********************************
        Fixed Social Media Sidebar
    **********************************/

    .tc-social-sidebar {

        .et_pb_image {
            background-color: #f6f6f6;
            transition: all ease-in-out .5s;

            img {
                filter: invert(13%) sepia(26%) saturate(1178%) hue-rotate(161deg) brightness(22%) contrast(92%);
                width: 38px;
            }
        }

        .et_pb_image:hover {
            background-color: $dark-blue;

            img {
                filter: none;
            }
        }
    }
    
    /*********************************
        Section Services
    **********************************/

    .tc-home-services-section {

        .et_pb_row {
            display: flex;
            align-items: center;
        }

    }

    .tc-home-services {

        .et_pb_promo_description {
            padding-bottom: 0;
        }

        h3 {
            text-transform: uppercase;
            font-size: 3.75rem;
            line-height: .8em;
            letter-spacing: -2.1px;
        }

        p {
            font-size: clamp(1rem, 1.525vw, 1.875rem);
        }
    }

    .tc-home-offices {
        h3 {
            color: $dark-blue;
            line-height: 1.01em;
            margin-bottom: 0;

        }

        .et_pb_button {
            clip-path: polygon(88% 0, 100% 50%, 100% 100%, 0 100%, 0 0);
            padding: 1.375em 2.25em;

        }

        .et_pb_promo_description {
            margin-bottom: 2.5em;
        }

    }

    .et_pb_searchsubmit {
        //clip-path: polygon(88% 0, 100% 50%, 100% 100%, 0 100%, 0 0);
    }
}